
import { useNavigate, useLocation } from 'react-router-dom';
import { useEffect } from 'react';

const Navigationcode = () => {
  const navigate = useNavigate();
  const { hash } = useLocation();

  // Scroll to section on hash change
  useEffect(() => {
    if (hash) {
      const section = document.querySelector(hash);
      section?.scrollIntoView({ behavior: "smooth" });
    }
  }, [hash]);

  // Navigation functions
  const goCreate = () => {
    window.scrollTo({ top: 0, behavior: 'smooth' });
    navigate('/create');
  };

  const goHome = () => {
    window.scrollTo({ top: 0, behavior: 'smooth' });
    navigate('/');
  };

  const goSteps = () => navigate('/#steps-target');
  const goTemplates = () => navigate('/#templates-target');
  const goResumeForm = () => navigate('/create#resumeform-target');
  const goResumeButton = () => navigate('/create#resumebutton-target');

  const scrollToTop = () => {
    window.scrollTo({ top: 0, behavior: 'smooth' });
  };

  const scrollToBottom = () => {
    window.scrollTo({ top: document.documentElement.scrollHeight, behavior: 'smooth' });
  };

  return {
    goCreate,
    goHome,
    goSteps,
    goTemplates,
    goResumeForm,
    scrollToTop,
    scrollToBottom,
    goResumeButton
  };
};

export default Navigationcode;
